@import url(https://fonts.googleapis.com/css?family=Roboto|Suez+One);
* {
  margin: 0;
  padding: 0;
  outline: none;
}

html, body, #root {
  width: 100%;
  height: 100%;
}


body {
  background-color: #fafafa;
  font-family: Trebuchet MS, Helvetica;
}
header {
  background-color: #55574e;
  width: 97%;
  padding: 12px 1.5% 12px;
  float: left;
  position: fixed;
  z-index: 2000;
}

header h2 {
  color: #fff;
  float: left;
  cursor: pointer;
}

header button {
  float: right;
}
.sort-direction-switcher > div {
  height: 25px;
  width: 25px;
  background-size: 75%;
  background-repeat: no-repeat;
  background-position: center;
  padding: 6px;
  cursor: pointer;
}

.sort-direction-switcher .asc {
  background-image: url(data:image/png;base64,);
}

.sort-direction-switcher .desc {
  background-image: url(data:image/png;base64,);
}
.filter {
  margin-bottom: 15px;
  float: left;
}

.filter > div {
  
  float: left;
}
.task {
  padding: 10px 0.5%;
  margin-bottom: 10px;
  background-color: #f3e5ab91;
  border-radius: 3px;
}

.task .author {
  color: #999966;
  font-size: 0.9em;
}

.task .author > span {
  display: block;
}

.task .edit-button {
  height: 14px;
  width: 14px;
  background-image: url(/BeeJee-test-app/static/media/edit.d57f173e.svg);
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  float: right;
}

.task .check {
  height: 15px;
  width: 15px;
  background-size: 100%;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: center;
  padding: 6px;
  cursor: pointer;
  float: left;
}

.task + .check + .author, .task + .check + .author + .text {
  margin-left: 30px;
}
#task-list {
  margin-bottom: 20px;
}

#task-list .tools {
  width: 100%;
  height: 50px;
}

#task-list .tools .add-task-btn {
  float: right;
}

#task-list .el-pagination .el-pager li.active {
  border-color: #999966;
  background-color: #999966;
}

#task-list .el-pagination {
  text-align: center;
}

#task-list .not-tasks-msg {
  text-align: center;
  color: #999966;
}
main {
  height: calc(100% - 82px);
  background-color: #fffdd0;
  padding: 82px 1.5% 0;
}
